* {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

:root {
  --items: 3;
  --indent: 30px;
}

.stripe {
  background-image: repeating-linear-gradient(-45deg, #3f62c5, #3f62c5 15px, #629bf1 15px, #629bf1 30px);
  padding: 10px;
}

.stripe-reverce {
  width: 100%;
  background-image: repeating-linear-gradient(45deg, #3f62c5, #3f62c5 15px, #629bf1 15px, #629bf1 30px);
  padding: 15px;
}

.wrapper {
  justify-content: center;
  display: flex;
}

.search-form {
  width: 150px;
  gap: 0;
  margin-right: 20px;
  transition: width .3s ease-in-out;
  display: flex;
  position: relative;
}

.search-form.js-focus {
  width: 200px;
}

.submit-btn {
  width: auto;
  height: 100%;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  position: absolute;
  top: 2px;
  left: auto;
  right: 5px;
}

.icon {
  height: 15px;
}

input {
  height: 100%;
  width: 100%;
  color: #fff;
  background-color: rgba(0, 0, 0, .1);
  border: 1px solid rgba(250, 250, 250, .5);
  border-radius: 15px;
  padding: 5px 12px;
  font-size: 14px;
}

input::-ms-input-placeholder {
  color: rgba(250, 250, 250, .5);
  font-size: 13px;
  line-height: 26px;
  transition: opacity .3s ease-in-out;
}

input::placeholder {
  color: rgba(250, 250, 250, .5);
  font-size: 13px;
  line-height: 26px;
  transition: opacity .3s ease-in-out;
}

input:focus {
  background-color: rgba(250, 250, 250, .1);
}

input:focus::-ms-input-placeholder {
  opacity: 0;
}

input:focus::placeholder {
  opacity: 0;
}

input:focus-visible {
  outline-offset: 0px;
  outline: rgba(0, 0, 0, 0);
}

.knobs, .layer {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.button {
  width: 50px;
  height: 25px;
  margin: -12px auto 0;
  position: relative;
  top: 50%;
  overflow: hidden;
}

.button.r, .button.r .layer {
  border-radius: 100px;
}

.button.b2 {
  border-radius: 2px;
}

.checkbox {
  width: 100%;
  height: 100%;
  opacity: 0;
  cursor: pointer;
  z-index: 3;
  margin: 0;
  padding: 0;
  position: relative;
}

.knobs {
  z-index: 2;
}

.layer {
  width: 100%;
  z-index: 1;
  background-color: rgba(250, 250, 250, .8);
  transition: all .3s;
}

#button-3 .knobs:before {
  content: "✓";
  width: 11px;
  height: 1px;
  color: #fff;
  text-align: center;
  background-color: #03a9f4;
  border-radius: 50%;
  padding: 8px 3px;
  font-size: 7px;
  font-weight: bold;
  line-height: 0;
  transition: all .3s, left .3s cubic-bezier(.18, .89, .35, 1.15);
  position: absolute;
  top: 4px;
  left: 4px;
}

#button-3 .checkbox:active + .knobs:before {
  width: 46px;
  border-radius: 100px;
}

#button-3 .checkbox:checked:active + .knobs:before {
  margin-left: -26px;
}

#button-3 .checkbox:checked + .knobs:before {
  content: "❌";
  background-color: #cfcfcf;
  font-size: 7px;
  line-height: 2px;
  left: 29px;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 20px 15px 80px;
}

@media (min-width: 320px) {
  .container {
    width: 480px;
  }
}

@media (min-width: 768px) {
  .container {
    width: 768px;
  }
}

@media (min-width: 1200px) {
  .container {
    width: 1200px;
  }
}

.togler-text {
  color: rgba(250, 250, 250, .9);
  margin-left: 15px;
  font-size: 13px;
  line-height: 26px;
}

@media (min-width: 768px) {
  .gallery {
    flex-wrap: wrap;
    gap: 30px;
    display: flex;
  }
}

.card-item {
  color: rgba(0, 0, 0, .8);
  width: 100%;
  height: 280px;
  border-radius: 4px;
  text-decoration: none;
  display: block;
  overflow: hidden;
  box-shadow: 0 1px 1px rgba(0, 0, 0, .12), 0 4px 4px rgba(0, 0, 0, .06), 1px 4px 6px rgba(0, 0, 0, .16);
}

@media (max-width: 767px) {
  .card-item:not(:last-child) {
    margin-bottom: 30px;
  }
}

@media (min-width: 768px) {
  .card-item {
    --items: 2;
    --indent: 30px;
    width: calc((100% - var(--indent) * (var(--items)  - 1) ) / var(--items) );
    margin-bottom: 0;
  }
}

@media (min-width: 1260px) {
  .card-item {
    --items: 4;
    --indent: 30px;
    width: calc((100% - var(--indent) * (var(--items)  - 1) ) / var(--items) );
    margin-bottom: 0;
  }
}

.img-prew {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

p {
  max-width: fit-content;
  margin: 0;
  font-size: 13px;
}

.img-info {
  height: 80px;
  text-align: center;
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: 10px;
  display: flex;
}

.load-more-btn {
  color: rgba(250, 250, 250, .7);
  cursor: pointer;
  background-color: rgba(0, 0, 0, .1);
  border: 1px solid rgba(250, 250, 250, .5);
  border-radius: 12px;
  margin: 0 auto;
  padding: 10px;
  display: block;
}

.load-more-btn:enabled:hover {
  background-color: rgba(250, 250, 250, .1);
}

.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  transform: translateX(-100%)scale(0);
}

.observer {
  height: 1px;
  width: 100%;
  background-color: rgba(0, 0, 0, 0);
}

body.hidden-scroll {
  overflow: hidden;
}

.sl-overlay {
  z-index: 1035;
  background: #fff;
  display: none;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.sl-wrapper {
  z-index: 1040;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

.sl-wrapper * {
  box-sizing: border-box;
}

.sl-wrapper button {
  cursor: pointer;
  background: none;
  border: 0;
  padding: 0;
  font-size: 28px;
}

.sl-wrapper button:hover {
  opacity: .7;
}

.sl-wrapper .sl-close {
  z-index: 10060;
  height: 44px;
  width: 44px;
  color: #000;
  margin-top: -14px;
  margin-right: -14px;
  font-family: Arial, Baskerville, monospace;
  font-size: 3rem;
  line-height: 44px;
  display: none;
  position: fixed;
  top: 30px;
  right: 30px;
}

.sl-wrapper .sl-counter {
  z-index: 10060;
  color: #000;
  font-size: 1rem;
  display: none;
  position: fixed;
  top: 30px;
  left: 30px;
}

.sl-wrapper .sl-download {
  width: 100%;
  text-align: center;
  z-index: 10060;
  color: #fff;
  font-size: 1rem;
  display: none;
  position: fixed;
  bottom: 5px;
}

.sl-wrapper .sl-download a {
  color: #fff;
}

.sl-wrapper .sl-navigation {
  width: 100%;
  display: none;
}

.sl-wrapper .sl-navigation button {
  height: 44px;
  width: 22px;
  text-align: center;
  z-index: 10060;
  color: #000;
  margin-top: -22px;
  font-family: Arial, Baskerville, monospace;
  line-height: 44px;
  display: block;
  position: fixed;
  top: 50%;
}

.sl-wrapper .sl-navigation button.sl-next {
  font-size: 2rem;
  right: 5px;
}

.sl-wrapper .sl-navigation button.sl-prev {
  font-size: 2rem;
  left: 5px;
}

@media (min-width: 35.5em) {
  .sl-wrapper .sl-navigation button {
    width: 44px;
  }

  .sl-wrapper .sl-navigation button.sl-next {
    font-size: 3rem;
    right: 10px;
  }

  .sl-wrapper .sl-navigation button.sl-prev {
    font-size: 3rem;
    left: 10px;
  }
}

@media (min-width: 50em) {
  .sl-wrapper .sl-navigation button {
    width: 44px;
  }

  .sl-wrapper .sl-navigation button.sl-next {
    font-size: 3rem;
    right: 20px;
  }

  .sl-wrapper .sl-navigation button.sl-prev {
    font-size: 3rem;
    left: 20px;
  }
}

.sl-wrapper.sl-dir-rtl .sl-navigation {
  direction: ltr;
}

.sl-wrapper .sl-image {
  -ms-touch-action: none;
  touch-action: none;
  z-index: 10000;
  position: fixed;
}

.sl-wrapper .sl-image img {
  width: 100%;
  height: auto;
  border: 0;
  margin: 0;
  padding: 0;
  display: block;
}

@media (min-width: 35.5em) {
  .sl-wrapper .sl-image img {
    border: 0;
  }
}

@media (min-width: 50em) {
  .sl-wrapper .sl-image img {
    border: 0;
  }
}

.sl-wrapper .sl-image iframe {
  background: #000;
  border: 0;
}

@media (min-width: 35.5em) {
  .sl-wrapper .sl-image iframe {
    border: 0;
  }
}

@media (min-width: 50em) {
  .sl-wrapper .sl-image iframe {
    border: 0;
  }
}

.sl-wrapper .sl-image .sl-caption {
  color: #fff;
  background: rgba(0, 0, 0, .8);
  padding: 10px;
  font-size: 1rem;
  display: none;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.sl-wrapper .sl-image .sl-caption.pos-top {
  top: 0;
  bottom: auto;
}

.sl-wrapper .sl-image .sl-caption.pos-outside {
  bottom: auto;
}

.sl-spinner {
  height: 40px;
  opacity: 0;
  width: 40px;
  z-index: 1007;
  -ms-animation: pulsate 1s ease-out infinite;
  border: 5px solid #333;
  border-radius: 40px;
  margin: -20px 0 0 -20px;
  animation: pulsate 1s ease-out infinite;
  display: none;
  position: fixed;
  top: 50%;
  left: 50%;
}

.sl-scrollbar-measure {
  width: 50px;
  height: 50px;
  position: absolute;
  top: -9999px;
  overflow: scroll;
}

.sl-transition {
  transition: transform .2s;
}

@keyframes pulsate {
  0% {
    opacity: 0;
    transform: scale(.1);
  }

  50% {
    opacity: 1;
  }

  100% {
    opacity: 0;
    transform: scale(1.2);
  }
}

